@media only screen and (max-width: 800px) {
  main {
    grid-template-columns: 1fr;
    grid-template-rows: var(--select-height) 1fr;
    grid-template-areas:
      'pattern_select'
      'canvas';
  }

  body[data-pattern] main {
    grid-template-columns: 1fr;
    grid-template-rows: var(--select-height) 1fr var(--player-mobile-height);
    grid-template-areas:
      'pattern_select'
      'canvas'
      'player';
  }

  .dialog_sidebar_form main,
  .dialog_info main {
    grid-template-rows: var(--select-height) 100vw;
    grid-template-areas:
      'pattern_select'
      'canvas';
  }

  body[data-pattern].dialog_sidebar_form main,
  body[data-pattern].dialog_info main {
    grid-template-columns: 1fr;
    grid-template-rows: var(--select-height) min(100vw, 50vh) 1fr;
    grid-template-areas:
      'pattern_select'
      'canvas'
      'config';
  }

  body[data-pattern].dialog_info main {
    grid-template-rows: var(--select-height) 70vw 1fr;
  }

  #main_header {
    display: none;
  }

  #sidebar_form {
    border-right: none;
    border-top: var(--border-light);
    box-shadow: 0 -5px 8px rgb(0 0 0 / 40%), 0 -1px 0 black;
    display: none;
    padding-top: 0;
  }

  #controls_panel {
    margin-inline-start: 56px;
  }
  #pattern_select_panel {
    padding-inline-start: 8px;
  }

  .dialog_sidebar_form #sidebar_form {
    display: block;
  }

  .dialog_sidebar_form #player,
  .dialog_info #player {
    display: none !important;
  }

  .buttons_dialog {
    width: 100vw;
  }

  .small_only {
    display: inherit !important;
  }

  .circular_btn:not(.active):hover {
    background: transparent;
  }
}

@media only screen and (min-width: 801px) {
  .large_only {
    display: inherit !important;
  }

  #info {
    grid-area: info;
    border-right: var(--border-light);
    box-shadow: 0 -2px 6px rgb(0 0 0 / 30%);
  }
}
